.editor-styles-wrapper .wp-block {
  border: 1px dashed #999;
  color: #F7F7F7;
}

.wp-block {
  max-width: 840px;
}

.wp-block[data-align=wide] {
  max-width: 1080px;
}

.wp-block[data-align=full] {
  max-width: none;
}

.rich-text.block-editor-rich-text__editable.wp-block-button__link:visited {
  border-radius: 0;
  color: #00AEC3;
  border-color: #00AEC3;
  border: 1px solid;
  background-color: white;
}

.editor-styles-wrapper {
  background: #1b1b1b;
}
.editor-styles-wrapper p {
  font: Objektiv VF, sans-serif;
  color: #F7F7F7;
}
.editor-styles-wrapper h2.wp-block-heading {
  font: Objektiv VF, sans-serif;
  color: #F7F7F7;
}
.editor-styles-wrapper .hmax {
  text-transform: uppercase;
  font-weight: 800;
}
.editor-styles-wrapper .inner-max-width {
  margin: calc((100vw - 1330px) / 2);
  max-width: 1330px;
}